}

form input[type = "text"]:focus {
  background-color: #e4eeea;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  outline: none;
  width: 100%;
}

#login-button {
  background-color: #43b293;
  color: ghostwhite;
  border: none;
  border-radius: 5px;
  width: 40%;
  cursor: pointer;
  font-size: 0.9em;
  padding: 2.5%;
  transition: 0.4s width ease-in-out;
}

#login-button:hover {
  background-color: #99ceb8;
  color: black;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  width: 55%;
}

#login-button:disabled {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
  border: none;
  border-radius: 8px;
  width: 30%;
  font-size: 1em;
  padding: 2.5%;
  transition: 0.4s width ease-in-out;
}

#login-button:disabled:hover {
  background-color: rgba(170, 170, 170, 0.466);
  color: ghostwhite;
  cursor: default;
  box-shadow: none;
  width: 30%;
}
